<template>
  <div class="essay-writing-page">
    <div class="page-bg">
      <div class="page-bg-image" />
      <div class="page-bg-gradient" />
    </div>
    <div class="essay-inner">
      <div v-if="loading" class="loading-wrap">
        <el-icon class="is-loading" :size="32"><Loading /></el-icon>
      </div>
      <template v-else>
        <aside class="sidebar">
          <h2 class="sidebar-title header-font">《文心雕龙》</h2>
          <nav class="category-nav">
            <button
              v-for="cat in categories"
              :key="cat.id"
              type="button"
              class="category-btn"
              :class="{ active: selectedCategory === cat.id }"
              @click="selectedCategory = cat.id"
            >
              {{ cat.title }}
            </button>
          </nav>
        </aside>
        <main class="main-content">
          <template v-if="selectedCategory === 'introduction'">
            <div class="intro-card content-font">
              <h1 class="intro-title header-font">{{ intro?.title }}</h1>
              <div class="intro-body">
                <p class="intro-p">{{ intro?.introduction?.summary }}</p>
                <p class="intro-p">{{ intro?.introduction?.showcase }}</p>
                <div class="intro-section">
                  <h2 class="header-font">重要性</h2>
                  <p class="intro-p">{{ intro?.significance?.content }}</p>
                </div>
              </div>
            </div>
          </template>
          <template v-else>
            <div class="items-list">
              <el-card
                v-for="item in currentItems"
                :key="item.id"
                class="content-card"
                shadow="hover"
              >
                <p class="original content-font">{{ item.originalText }}</p>
                <p class="source">{{ item.source }}</p>
                <p class="translation">{{ item.translation }}</p>
                <el-collapse>
                  <el-collapse-item title="解析" name="analysis">
                    <p class="analysis content-font">{{ item.analysis }}</p>
                  </el-collapse-item>
                </el-collapse>
              </el-card>
            </div>
          </template>
        </main>
      </template>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { Loading } from '@element-plus/icons-vue'

interface IntroData {
  title: string
  introduction: { summary: string; showcase: string }
  significance: { content: string }
}

interface ChapterItem {
  id: string
  source: string
  originalText: string
  translation: string
  analysis: string
}

interface ChaptersData {
  categories: { id: string; title: string }[]
  items: Record<string, ChapterItem[]>
}

const intro = ref<IntroData | null>(null)
const chapters = ref<ChaptersData | null>(null)
const loading = ref(true)
const selectedCategory = ref('introduction')

const categories = computed(() => chapters.value?.categories ?? [])
const currentItems = computed(() => {
  if (!chapters.value || selectedCategory.value === 'introduction') return []
  return chapters.value.items[selectedCategory.value] ?? []
})

onMounted(async () => {
  try {
    const base = import.meta.env.BASE_URL || '/'
    const [introRes, chaptersRes] = await Promise.all([
      fetch(`${base}classic/introduction.json`),
      fetch(`${base}classic/chapters.json`),
    ])
    if (introRes.ok) intro.value = await introRes.json()
    if (chaptersRes.ok) chapters.value = await chaptersRes.json()
  } finally {
    loading.value = false
  }
})
</script>

